@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  transition: background-color 0.3s, color 0.3s;
}

.app {
  min-height: 100vh;
}

@media (max-width: 768px) {
  .container {
    padding: 10px;
  }
}

/* Toggle Switch Custom Animation */
.toggle-switch {
  transition: border-color 0.4s, background-color 0.4s;
}
.toggle-switch-bg {
  transition: background-color 0.4s;
}

html.dark .toggle-switch {
  border: 2px solid #8E1616 !important;
}
html.dark .toggle-switch-bg {
  background-color: #020D0C !important;
}

html.light .toggle-switch {
  border: 2px solid #12372A !important;
}
html.light .toggle-switch-bg {
  background-color: #FBFADA !important;
}