body.light {
  background-color: #FBFADA;
  color: #12372A;
}

.light .card {
  background-color: #ADBC9F;
  box-shadow: 0 4px 8px rgba(18, 55, 42, 0.2);
}

.light .header,
.light .contact footer {
  background-color: #436850;
  color: #FBFADA;
}

.light button {
  background-color: #12372A;
  color: #FBFADA;
}

.light input,
.light textarea {
  background-color: #ADBC9F;
  border-color: #436850;
  color: #12372A;
}

.light input:focus,
.light textarea:focus {
  outline: none;
  ring: 2px solid #436850;
}